export default class Banner {
  constructor() {
    this.newViewContent = document.querySelector('.news-view__content-wrapper');
    this.banners = this.newViewContent.querySelectorAll(
      '.article-page__banner'
    );
    this.bigDesktop = 1360;
    this.maxContentHeight = 1000;
  }

  setPosition() {
    const newViewContentHeight = this.newViewContent.offsetHeight;
    const bannerHeight = this.banners[0].offsetHeight;

    if (newViewContentHeight + bannerHeight < this.maxContentHeight) {
      this.banners[0].style.top = '50%';
      this.banners[0].style.transform = 'translateY(-50%)';
    }
  }

  init() {
    if (this.banners && window.innerWidth >= this.bigDesktop) {
      this.setPosition();
    }
  }
}
