export default class Slider {
  constructor() {
    this.slider = document.querySelector('.article__content-slider');
  }

  toArray(nodeList) {
    const arr = [];
    nodeList.forEach(n => arr.push(n));
    return arr;
  }

  sliderPaging({
    dots,
    slides,
    paging,
    dotSize,
    dotClasses,
    buttonPrev,
    buttonNext,
    slidingWrap,
    nthLastSlide,
    visibleRange,
    nthFirstSlide,
    activeSlideClass
  }) {
    let nthActiveSlide = 0;

    onNthActiveSlide(nthActiveSlide);

    function nthActiveSlideSwap(f) {
      onNthActiveSlide(nthActiveSlide = f(nthActiveSlide));
    }

    function nthActiveSlideReset(n) {
      onNthActiveSlide(nthActiveSlide = n);
    }

    function onNthActiveSlide(nthActiveSlide) {
      updatePaging(nthActiveSlide);
      updateSlides(nthActiveSlide);
    }

    function clamp(number, min, max) {
      return Math.max(min, Math.min(number, max));
    }

    const staticDots = slidingWrap.offsetWidth < paging.offsetWidth;

    if (staticDots) {
      slidingWrap.style.left = `${(paging.offsetWidth - slidingWrap.offsetWidth) * 0.5}px`;
    }

    function updatePaging(nthActiveSlide) {
      const windowPosition = clamp(nthActiveSlide, nthFirstSlide + visibleRange, nthLastSlide - visibleRange);
      const windowRangeStart = windowPosition - visibleRange;
      const windowRangeEnd = windowPosition + visibleRange;

      if (!staticDots) {
        slidingWrap.style.left = `${windowRangeStart * -dotSize}px`;
      }

      dots.forEach((dot, nthDot) => {
        let distance = Math.abs(nthActiveSlide - nthDot);
        if (windowRangeStart <= nthDot && nthDot <= windowRangeEnd) {
          distance = Math.min(distance, 2);
        }
        dot.classList.remove(...dotClasses);
        const addClass = dotClasses[distance];
        if (addClass) {
          dot.classList.add(addClass);
        }
      });
    }

    function updateSlides(nthActiveSlide) {
      slides.forEach((slide, nthSlide) => {
        if (nthActiveSlide === nthSlide) {
          slide.classList.add(activeSlideClass);
        }
        else {
          slide.classList.remove(activeSlideClass);
        }
      });
    }

    buttonNext.addEventListener('mousedown', (e) => {
      e.stopPropagation();
      e.preventDefault();
      nthActiveSlideSwap(a => clamp(a + 1, nthFirstSlide, nthLastSlide));
    });

    buttonPrev.addEventListener('mousedown', (e) => {
      e.stopPropagation();
      e.preventDefault();
      nthActiveSlideSwap(a => clamp(a - 1, nthFirstSlide, nthLastSlide));
    });

    dots.forEach((d, nthDot) => {
      d.addEventListener('mousedown', (e) => {
        e.stopPropagation();
        e.preventDefault();
        nthActiveSlideReset(nthDot);
      });
    });

  }

  _sliderInit(slider) {
    const slides = this.toArray(slider.querySelectorAll('.article__content-slider__slide'));
    const dots = this.toArray(slider.querySelectorAll('.article__content-slider__paging__dot-wrap'));
    const paging = slider.querySelector('.article__content-slider__paging');
    const slidingWrap = slider.querySelector('.article__content-slider__paging__sliding-wrap');
    const buttonPrev = slider.querySelector('.article__content-slider__button_prev');
    const buttonNext = slider.querySelector('.article__content-slider__button_next');
    const dotSize = 20;

    this.sliderPaging({
      dots,
      slides,
      paging,
      dotSize,
      dotClasses: ['article__content-slider__paging__dot-wrap_d0', 'article__content-slider__paging__dot-wrap_d1', 'article__content-slider__paging__dot-wrap_d2'],
      buttonPrev,
      buttonNext,
      slidingWrap,
      nthLastSlide: slides.length - 1,
      visibleRange: 2,
      nthFirstSlide: 0,
      activeSlideClass: 'article__content-slider__slide_active'
    });
  }

  init() {
    this._sliderInit(this.slider);
    console.log('hi')
  }
}