export default class Footer {

  constructor() {
    this.switchInput = document.querySelector('.switch__input');
    this.switchName = document.querySelector('.switch__name');
  }

  _checkCheckbox(element) {
    return this.switchName.innerHTML = element.checked ? '' : '';
  }

  init() {

    if (this.switchInput) {
      this.switchInput.addEventListener('click', e => this._checkCheckbox(e.target));
    }

  }
}
